.primary-btn {
  display: block;
  border-radius: 16px;
  background: var(--gradients-dark-ether, linear-gradient(101deg, #5645FE 0%, #7940EF 100%));
  padding: 16px 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
  letter-spacing: -0.32px;

  &:hover {
    cursor: pointer;
  }
}

.secondary-btn {
  display: block;
  border-radius: 16px;
  padding: 16px 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
  letter-spacing: -0.32px;

  &:hover {
    cursor: pointer;
  }
}

// 

.title {
  color: var(--primary-white-100, #FFF);
  text-align: center;
  font-size: 52px;
  font-weight: 600;
  line-height: 123.077%;
  letter-spacing: -2.6px;
  margin-bottom: 40px;
}

.descr {
  color: var(--gray-500, #757575);
  text-align: center;
  font-size: 18px;
  font-weight: 500;
  line-height: 155.556%;
  letter-spacing: -0.36px;
  max-width: 540px;
}

// 

header {
  padding: 196px 0px 96px 0px;

  .nav-bar {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    height: 100px;
    width: 100%;
    background: #000;

    .logo {
      flex: 1;
      display: flex;
      align-items: center;
      gap: 8px;
    }

    .nav {
      display: flex;
      justify-content: center;
      flex: 1;

      &-list {
        display: flex;

        a {
          padding: 16px 20px;
          font-size: 16px;
          font-weight: 600;
          line-height: 150%;
          letter-spacing: -0.32px;
        }
      }
    }

    .nav-right {
      display: flex;
      justify-content: end;
      flex: 1;

      .primary-btn {

        width: 142px;
      }
    }
  }

  .header-content {
    .header-title {
      text-align: center;
      font-size: 96px;
      font-weight: 600;
      line-height: 116.667%;
      letter-spacing: -3.84px;
      max-width: 816px;
      margin: 0 auto;
      margin-bottom: 40px;
    }

    .header-descr {
      color: var(--gray-500, #757575);
      text-align: center;
      font-size: 18px;
      font-weight: 500;
      line-height: 155.556%;
      letter-spacing: -0.36px;
      max-width: 540px;
      margin: 0 auto;
      margin-bottom: 80px;
    }

    .primary-btn {
      width: 264px;
      margin: 0 auto;
      margin-bottom: 8px;
    }

    .secondary-btn {
      width: 264px;
      margin: 0 auto;
    }
  }
}

