.advantages {

  .title {
    max-width: 816px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  .descr {
    margin: 0 auto;
    margin-bottom: 64px;
  }

  &-wrapper {
    display: flex;
  }

  &-card {
    width: 350px;
  }

  &-subtitle {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    line-height: 133.333%;
    letter-spacing: -0.48px;
  }

  &-descr {
    color: var(--gray-500, #757575);
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: -0.32px;
  }

}